import {useState} from "react";
import LevelOption from "../component/LevelOption";
import PuzzleGame from "../component/PuzzleGame";
import img from "./lamborghini_square.jpg";

const levelEasy = {
	name: "Dễ",
	size: 3
};
const levelMedium = {
	name: "Trung Bình",
	size: 4
};
const levelHard = {
	name: "Khó",
	size: 6
};

const levels = [levelEasy, levelMedium, levelHard];

const ImagePuzzle = (props) => {
	const [level, setLevel] = useState(levelEasy);
	const handleLevelChange = (selectedLevel) => {
		setLevel(selectedLevel);
	};

	return (
		<div>
			<LevelOption levels={levels} value={level} onSelect={handleLevelChange}/>
			<PuzzleGame size={6} imageSrc={img}/>
		</div>
	);
}

export default ImagePuzzle;
